.container {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 10px;
    border: 1px solid silver;
    background-color: whitesmoke;
    border-radius: 10px;
    box-shadow: rgba(2, 2, 128, 0.2) 12px 7px 29px 0px;
    color: black;
}

.cardHeading2 {
    margin: 0;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 10px 10px;
    color: black;
}

.image {
    position: relative;
    height: 30px;
    width: 30px;
}

img {
    width: 100%; 
    height: 100%;  
    border-radius: 50%;
}

.status {
    position: absolute;
    justify-content: center;
    right: 0;
    bottom: 0;
    width: 10px;
    height: 10px;
    background: rgb(77, 166, 79);
    border-radius: 50%;
}

.title {
    font-weight: 200;
    width: 300px;
    padding: 0px 15px;
    
}

.tags {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 20px;
    justify-content: space-between;
    gap: 5px;
    color: black;
    
}

.tag_for_cards {
    color: rgb(148, 146, 146);
    width: max-content;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid silver;
}
